.container {
  width: 93%;
  height: 7.5rem;
  margin: 1rem 0;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;

  .image {
    position: relative;

    width: 50%;
    height: 100%;
    
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    p {
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: .9rem;
      margin: .25rem .45rem;
      padding: .08rem .15rem;
      background-color: rgba(0, 0, 0, 0.822);
      border-radius: .15rem;
      color: white;
    }
  }

  .text {
    width: 47%;
    height: 100%;
    padding-left: .5rem;

    h1 {
      font-size: 1.1rem;
      margin: 0;
      padding: 0;
    }

    p {
      font-size: .8rem;
      margin: .15rem 0;
      padding: 0;
    }

    .date {
      margin-top: .45rem;
    }
  }
}