.header {
  position: fixed;
  z-index: 999;
  top: 0;

  width: 100%;
  height: 3.5rem;
  padding: .5rem;
  
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: white;
  -webkit-box-shadow: 0px 0px 15px 6px rgba(0,0,0,0.5); 
  box-shadow: 0px 0px 15px 6px rgba(0,0,0,0.5);

  .container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
    h1 {
      color: #000000;
      font-size: 1.15rem;
      font-weight: 900;
    }

    div {
      margin-left: .5rem;
    }
  }
}