.panel {
  z-index: 100;
  width: 100%;
  display: flex;
  flex-direction: column;

  -webkit-box-shadow: 0px 0px 15px 6px rgba(0,0,0,0.5); 
  box-shadow: 0px 0px 15px 6px rgba(0,0,0,0.5);

  .background {
    position: relative;
    margin-top: 3.3rem;
    margin-bottom: 2rem;
  
    width: 100%;
    height: 5rem;
  
    background-image: url('../../../graphic/background.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
  
    .avatar {
      width: 5rem;
      height: 5rem;
  
      position: absolute;
      bottom: -2rem;
      left: 1.3rem;
  
      background-image: url('../../../graphic/avatar.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
  
      border-radius: 50%;
    }
  }

  h1 {
    margin: .5rem 0 .5rem 2rem;
    padding: 0;
  }

  .subscribers-container {
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    .subscriber-button {
      height: 2rem;
      padding: 0 .35rem;

      display: flex;
      align-items: center;
      justify-content: center;

      background-color: rgb(44, 44, 44);
      border-radius: .4rem;
      border: none;
      font-weight: 100;

      h3 {
        color: white;
        font-size: 1rem;
        padding: 0;
        margin-right: .25rem;
      }
    }

    

    p {
      margin-left: .5rem;
      font-size: .9rem;
    }
  }

  ul {
    margin: 1rem 0 0 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    li {
      padding: .75rem 1rem;
      font-size: 1.07rem;
    }

    .li-active {
      border-bottom: 2px solid red;
    }
  }
}